@tailwind base;
@tailwind components;
@tailwind utilities;

@layer {
  /* ----------------------------Yekan------------------------------- */

  @font-face {
    font-family: IranYekan;
    font-weight: 100;
    src: local(IranYekan), url('/src/assets/css/iranykan/ttf/IRANYekanWebThin.ttf') format('truetype');
  }

  @font-face {
    font-family: IranYekan;
    font-weight: 300;
    src: local(IranYekan), url('/src/assets/css/iranykan/ttf/IRANYekanWebLight.ttf') format('truetype');
  }
  @font-face {
    font-family: IranYekan;
    font-weight: normal;
    src: local(IranYekan), url('/src/assets/css/iranykan/ttf/IRANYekanWebRegular.ttf') format('truetype');
  }
  @font-face {
    font-family: IranYekan;
    font-weight: 500;
    src: local(IranYekan), url('/src/assets/css/iranykan/ttf/IRANYekanWebMedium.ttf') format('truetype');
  }
  @font-face {
    font-family: IranYekan;
    font-weight: 700;
    src: local(IranYekan), url('/src/assets/css/iranykan/ttf/IRANYekanWebBold.ttf') format('truetype');
  }
  @font-face {
    font-family: IranYekan;
    font-weight: 800;
    src: local(IranYekan), url('/src/assets/css/iranykan/ttf/IRANYekanWebExtraBold.ttf') format('truetype');
  }
  @font-face {
    font-family: IranYekan;
    font-weight: 900;
    src: local(IranYekan), url('/src/assets/css/iranykan/ttf/IRANYekanWebBlack.ttf') format('truetype');
  }
  @font-face {
    font-family: IranYekan;
    font-weight: 1000;
    src: local(IranYekan), url('/src/assets/css/iranykan/ttf/IRANYekanWebExtraBlack.ttf') format('truetype');
  }

  /* ----------------------------Roosta------------------------------- */
  @font-face {
    font-family: roosta2;
    font-weight: 300;
    src: local(roosta2), url('./assets/css/roosta/ZFInstaEF.ttf') format('truetype');
  }
  @font-face {
    font-family: roosta;
    font-weight: 300;
    src: local(roosta), url('./assets/css/roosta/ZFInsta.ttf') format('truetype');
  }

  /* -----------------------------DAY------------------------------- */
  @font-face {
    font-family: day;
    font-weight: 300;
    src: local(day), url('./assets/css/day/day.ttf') format('truetype');
  }
}

@layer components {

  .pages {
    @apply max-w-sm mx-auto h-screen bg-gradient-to-tr from-lemon-500 to-coral-500
  }

  .header {
    @apply fixed top-0 right-0 left-0 p-2  bg-white bg-opacity-20 backdrop-blur-md
           flex justify-between items-center z-40;
  }
  .navbar {
    @apply fixed bottom-4 right-6 left-6 py-4 px-2 z-40
           bg-white bg-opacity-20 backdrop-blur-lg rounded-xl;
  }
  .navbar .content {
    @apply relative
           flex justify-around items-center z-40
  }
  .circle {
    @apply relative h-full w-1/5 bg-lemon-600
  }

}

body {
  margin: 0;
  font-family: IranYekan , -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
